import React from 'react'
import { CloseIcon, Icon, SidebarBtnWrap, SidebarContainer, SidebarLink, SidebarMenu, SidebarRoute } from './SidebarElements'

const Sidebar = ({ isOpen, toggle }) => {
    return (
        <SidebarContainer isOpen={isOpen} onClick={toggle}>
            <Icon onClick={toggle}>
                <CloseIcon />
            </Icon>
            <SidebarMenu>
                <SidebarLink to="/">Pizzalar</SidebarLink>
                <SidebarLink to="/">Dessertlar</SidebarLink>
                <SidebarLink to="/">To'liq Menu</SidebarLink>
            </SidebarMenu>
            <SidebarBtnWrap>
                <SidebarRoute to="/">Buyurtma Berish</SidebarRoute>
            </SidebarBtnWrap>

        </SidebarContainer>
    )
}

export default Sidebar
