import React from "react";

import {
    FaFacebook,
    FaInstagram,
    FaYoutube,
    FaTwitter,
    FaLinkedin
} from 'react-icons/fa'

import {
  FooterContainer,
  FooterWrap,
  SocialIconLink,
  SocialIcons,
  SocialLogo,
  SocialMedia,
  SocialMediaWrap,
} from "./FooterElements";

const Footer = () => (
  <FooterContainer>
    <FooterWrap>
      <SocialMedia>
        <SocialMediaWrap>
          <SocialLogo to="/">Chopar</SocialLogo>
          <SocialIcons>
            <SocialIconLink
              href="https://www.facebook.com"
              target="_blank"
              aria-label="Facebook"
              rel="noonpener noreferrer"
            ><FaFacebook /></SocialIconLink>

            <SocialIconLink
              href="https://www.instagram.com"
              target="_blank"
              aria-label="Instagram"
              rel="noonpener noreferrer"
            ><FaInstagram /></SocialIconLink>

            <SocialIconLink
              href="https://www.youtube.com"
              target="_blank"
              aria-label="Youtube"
              rel="noonpener noreferrer"
            ><FaYoutube /></SocialIconLink>


            <SocialIconLink
              href="https://www.twitter.com"
              target="_blank"
              aria-label="Twitter"
              rel="noonpener noreferrer"
            ><FaTwitter /></SocialIconLink>

            <SocialIconLink
              href="https://www.linkedin.com"
              target="_blank"
              aria-label="Linkedin"
              rel="noonpener noreferrer"
            ><FaLinkedin /></SocialIconLink>
          </SocialIcons>
        </SocialMediaWrap>
      </SocialMedia>
    </FooterWrap>
  </FooterContainer>
);

export default Footer;
